export default {
  data: function data() {
    return {
      vacancyQuestion: {
        'Question': '',
        'Space1Answer': '',
        'Space2Answer': '',
        'Space3Answer': '',
        'Space4Answer': '',
        'KN': '',
        'Chapter': '',
        'difficulty': '',
        'Issequence': ''
      },
      options: [{
        value: 'Ch1',
        label: '第一章'
      }, {
        value: 'Ch2',
        label: '第二章'
      }, {
        value: 'Ch3',
        label: '第三章'
      }, {
        value: 'Ch4',
        label: '第四章'
      }, {
        value: 'Ch5',
        label: '第五章'
      }, {
        value: 'Ch6',
        label: '第六章'
      }, {
        value: 'Ch7',
        label: '第七章'
      }, {
        value: 'Ch8',
        label: '第八章'
      }, {
        value: 'Ch9',
        label: '第九章'
      }, {
        value: 'Ch10',
        label: '第十章'
      }, {
        value: 'Ch11',
        label: '第十一章'
      }],
      singleDecideQuestion: {
        'Question': '',
        'Item1': '',
        'Item2': '',
        'Item3': '',
        'Item4': '',
        'Answer': '',
        'KN': '',
        'Chapter': '',
        'difficulty': ''
      },
      applicationQuestion: {
        'fatherQuestion': '',
        'Entity1': '',
        'Entity2': '',
        'Entity3': '',
        'Entity4': '',
        'Entity5': '',
        'ChildQuestion1': '',
        'ChildQuestion2': '',
        'ChildQuestion3': '',
        'ChildQuestion4': '',
        'ChildQuestion5': '' // childQuestions:[
        //   {
        //     value:''
        //   }
        // ],
        // entityprops:[
        //     {
        //         entity:'',
        //         prop:'()'
        //     }
        // ],
        // sqls:[
        //     {
        //         select:'SELECT ',
        //         from:'FROM ',
        //         where:'WHERE ',
        //         group_by:'GROUP BY ',
        //         having:'HAVING '
        //     }
        // ]

      }
    };
  },
  methods: {
    onAddQuestionSubmit: function onAddQuestionSubmit() {
      if (this.$store.state.nowQuestionType == 'applicationQuestion') {
        this.$emit('getAddData', this.applicationQuestion);
      } else if (this.$store.state.nowQuestionType.substring(0, 7) == 'vacancy') {
        this.$emit('getAddData', this.vacancyQuestion);
      } else {
        this.$emit('getAddData', this.singleDecideQuestion);
      }

      this.$store.state.isFromShow = false;
    },
    removeChildQuestion: function removeChildQuestion(item) {
      var index = this.applicationQuestion.childQuestions.indexOf(item);

      if (index !== -1) {
        this.applicationQuestion.childQuestions.splice(index, 1);
      }
    },
    addChildQuestion: function addChildQuestion() {
      this.applicationQuestion.childQuestions.push({
        value: '',
        key: Date.now()
      });
    },
    addEntityProp: function addEntityProp() {
      this.applicationQuestion.entityprops.push({
        entity: '',
        prop: '()',
        key: Date.now()
      });
    },
    removeEntityProp: function removeEntityProp(item) {
      var index = this.applicationQuestion.entityprops.indexOf(item);

      if (index !== -1) {
        this.applicationQuestion.entityprops.splice(index, 1);
      }
    },
    addSQL: function addSQL() {
      this.applicationQuestion.sqls.push({
        select: 'SELECT ',
        from: 'FROM ',
        where: 'WHERE ',
        group_by: 'GROUP BY ',
        having: 'HAVING ',
        key: Date.now()
      });
    },
    removeSQL: function removeSQL(item) {
      var index = this.applicationQuestion.sqls.indexOf(item);

      if (index !== -1) {
        this.applicationQuestion.sqls.splice(index, 1);
      }
    }
  },
  props: {},
  mounted: function mounted() {
    if (this.$store.state.nowQuestionType == 'decide') {
      this.$store.state.updateItemDisabled = true;
    } else {
      this.$store.state.updateItemDisabled = false;
    }
  }
};